import React from 'react';
import classNames from 'classnames';
import Select from '../../../Views/Select';
import Text from '../../../helpers/Text';

// import DatePicker from 'react-date-picker/dist/entry.nostyle';
import '../../../Views/MyOrderViews/styles/celender.scss';
import '../../../Views/MyOrderViews/styles/datepicker.scss';
import Input from '../../../Views/Input';
import Icon from '../../../Views/Icon';
import { searchIcon } from '../../../images';
import PersonalPageViews from '../../../Views/PersonalPageViews';
import Button from '../../../Views/Button';

import style from '../../../Views/MyOrderViews/styles/index.module.scss';
import DatePicker from 'react-date-picker';
import InfoPayRedText from '../../../Views/PersonalPageViews/InfoPaidOrders/InfoPayRedText';
import InfoPayGreenText from '../../../Views/PersonalPageViews/InfoPaidOrders/InfoPayGreenText';

const BaseInfoOrder = ({
  total_orders_price_unpaid,
  total_orders_price_paid,
  clearAllFiltersOrders,
  changeStatusFilter,
  searchOrderForFio,
  changeValueSearch,
  selectCreateFrom,
  dateFilterData,
  selectCreateTo,
  currency,
  options,
  status
}) => {

  return (
    <div className={style['cabinet-content']}>
      <div className={style['cabinet-heading']}>
        <Text text={'my.orders'} />
        <div
          className={style['cabinet-heading__debt']}
        >
        </div>
      </div>

       {/* status and date */}
      <div className={style['cabinet-topfilter']}>
        <div className={style['cabinet-topfilter__left']}>
          <Select
            className={'select-order-list'}
            placeholder={status ? options.filter(el => el.value === status)[0]?.title : options[0]?.title}
            options={options}
            onClick={changeStatusFilter}
          />
        </div>

        <div className={style['wrapper_filter-group']}>
          <DatePicker
            clearIcon={null}
            // name = { 'created_at__gte'}
            onChange={e => selectCreateTo(e)}
            value={dateFilterData?.created_at__gte}
            format={'dd.MM.yyyy'}
            className={classNames({
              datepicker: true,
              [style['wrapper_filter-group__datepicker']]: true,
            })}
          />
          <span className={style['wrapper_filter-group__datepicker-hyphen']}>-</span>
          <DatePicker
            clearIcon={null}
            // name = { 'created_at__lte'}
            className={classNames({
              datepicker: true,
              [style['wrapper_filter-group__datepicker']]: true,
            })}
            onChange={e => selectCreateFrom(e)}
            value={dateFilterData?.created_at__lte}
            format={'dd.MM.yyyy'}
          />
        </div>
      </div>

      {/* search input and reset filter */}
      <div className={style['cabinet-topfilter__left']}>
        <div>
          <Input
            className={'cabinet-lk__search'}
            value={searchOrderForFio ? searchOrderForFio : ''}
            name={'search-order'}
            onChange={changeValueSearch}
            placeholder={'Введите имя получателя'}
            helpText={
              <Icon src={searchIcon} className={style['cabinet-topfilter__search-icon']} width={20} height={20} />
            }
          />
        </div>
        <Button
          className={style['cabinet-clear-filter-order']}
          onClick={clearAllFiltersOrders}
        >
          очистить фильтры
        </Button>
      </div>

      <PersonalPageViews.InfoPayWrapper>
        <PersonalPageViews.InfoPayGreyText>
          Сумма оплаченных заказов <InfoPayGreenText>{total_orders_price_paid ?? 0} {' '} {currency}</InfoPayGreenText>
        </PersonalPageViews.InfoPayGreyText>
        <PersonalPageViews.InfoPayGreyText>
          Сумма неоплаченных товаров <InfoPayRedText>{total_orders_price_unpaid ?? 0} {' '} {currency}</InfoPayRedText>
        </PersonalPageViews.InfoPayGreyText>
      </PersonalPageViews.InfoPayWrapper>
    </div>
  );
}; 

export default React.memo(BaseInfoOrder);
