import React, { memo } from 'react';
import { useStoreon } from 'storeon/react';
import OrdersPageLayoutContainer from './OrdersPageLayout/OrdersPageLayoutContainer';

const OrdersPage = ({
    context
}) => {
    const { currency, listOrdersLk, balanceStore} = useStoreon('currency', 'listOrdersLk', 'balanceStore');
    const {
        cabinet_site_menu,
        cabinet_menu,
        breadcrumbs,
        statuses,
        profile,
        shop,
    } = context;

    const { tableBodyData, dateFilterData, searchOrderForFio, isLoadingOrders, count, currentPage, status } = listOrdersLk;
    const { user = {}, role, passport, organization, links, id } = profile;
    const { balance, total_debt_orders, total_orders_price_unpaid, total_orders_price_paid } = balanceStore;
    const { username } = user;
    const amountNotifications = profile?.notifications;
    
    return (
        <>
            <OrdersPageLayoutContainer
                total_orders_price_unpaid={total_orders_price_unpaid}
                total_orders_price_paid={total_orders_price_paid}
                amountNotifications={amountNotifications}
                total_debt_orders={total_debt_orders}
                cabinet_site_menu={cabinet_site_menu}
                searchOrderForFio={searchOrderForFio}
                dateFilterData={dateFilterData}
                tableBodyData={tableBodyData}
                cabinet_menu={cabinet_menu}
                breadcrumbs={breadcrumbs}
                create_shop={shop?.shop_link}
                is_has_shop={shop?.is_has_shop}
                currentPage={currentPage}
                isLoading={!isLoadingOrders}
                countOrder= {count}
                profileId={id}
                status={status}
                username={username}
                currency={currency}
                statuses={statuses}
                profile={profile}
                balance={balance}
                shop={shop}
                role={role}
            />
        </>
    )
}

export default (OrdersPage);